}

.period {
    color: whitesmoke;
}

li {
    list-style-type: none;
}

a {
    color: whitesmoke;
    font-family: 'Red Rose', cursive;
    text-shadow: 2px 2px 2px black;
}

#backtt {
    font-size: 1.5em; 
}

hr {
    background-color: white;
}

#videoBox {
  margin: 20px;  
  margin-bottom: 40px;
  border: 5px solid white;
  border-radius: 15%;
}

#pro-pic {
    object-fit: fill;
    border: solid white 4px;
    border-radius: 25%;
}

/* media query to make the image smaller so that the text is readable */
@media screen and (max-width: 410px) {
    #pro-pic {
      width: 16em;
      height: 16em;
    }
    #videoBox {
        width: 220px;
        height: 220px;
    }
  }
#pro-pic {
    width: 24em;
    height: 24em;
    border: solid white 4px;
    border-radius: 25%;
    margin-bottom: 10px;
}

details {
    font-size: 1.5em;
    color: whitesmoke;
    font-family: 'Red Rose', cursive;
    text-shadow: 2px 2px 2px black;
    outline: none;
}

details:hover{
    cursor: pointer;
}

#donate {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#donatebox {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 10px;
    background-color: rgba(54, 53, 53, 0.493);
    width: 50%;

}

details {
    border: solid 2px white;
    border-radius: 3%;
    margin: 10px;
}



/* media query to make the image smaller so that the text is readable */
@media screen and (max-width: 410px) {
    #pro-pic {
      width: 16em;
      height: 16em;
    }

    #donatebox {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
  }
#foftext {
    color: black;
    background-color: white;
    font-family: "Gill Sans", sans-serif;
    border: 2px solid white;
